import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Button,
  TextField,
  Grid,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  IconButton,
  Chip,
  InputAdornment,
  TablePagination,
  Fab,
  Tooltip,
} from '@mui/material';
import {
  Search,
  Add,
  Edit,
  Visibility,
  People,
  Phone,
  CalendarToday,
  Assignment,
} from '@mui/icons-material';
import { patientsAPI, handleAPIError } from '../services/api';

// Sample data - in real app, this would come from API
const samplePatients = [
  {
    id: 1,
    patient_id: 'P000001',
    first_name: '',
    last_name: '',
    first_name_kana: '',
    last_name_kana: '',
    date_of_birth: '1978-05-15',
    gender: 'male',
    phone: '090-1234-5678',
    is_active: '1',
  },
  {
    id: 2,
    patient_id: 'P000002',
    first_name: '',
    last_name: '',
    first_name_kana: '',
    last_name_kana: '',
    date_of_birth: '1991-12-03',
    gender: 'female',
    phone: '080-9876-5432',
    is_active: '1',
  },
  {
    id: 3,
    patient_id: 'P000003',
    first_name: '',
    last_name: '',
    first_name_kana: '',
    last_name_kana: '',
    date_of_birth: '1956-08-22',
    gender: 'male',
    phone: '070-5555-1111',
    is_active: '1',
  },
];

const Patients = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [patients, setPatients] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  // 
  const fetchPatients = async () => {
    try {
      setLoading(true);
      setError('');
      const response = await patientsAPI.getPatients();
      console.log('Patients API response:', response.data);
      setPatients(response.data || []);
    } catch (err) {
      console.error('Error fetching patients:', err);
      const errorData = handleAPIError(err);
      setError(errorData.message);
      
      // : 
      console.log(': ');
      setPatients(samplePatients);
    } finally {
      setLoading(false);
    }
  };

  // 
  useEffect(() => {
    fetchPatients();
  }, []);

  // location 
  useEffect(() => {
    if (location.pathname === '/patients') {
      fetchPatients();
    }
  }, [location]);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleSearch = (event) => {
    setSearchTerm(event.target.value);
    setPage(0);
  };

  const filteredPatients = patients.filter((patient) => {
    const searchLower = searchTerm.toLowerCase();
    return (
      patient.patient_id.toLowerCase().includes(searchLower) ||
      patient.first_name.toLowerCase().includes(searchLower) ||
      patient.last_name.toLowerCase().includes(searchLower) ||
      patient.first_name_kana.toLowerCase().includes(searchLower) ||
      patient.last_name_kana.toLowerCase().includes(searchLower) ||
      patient.phone.includes(searchTerm)
    );
  });

  const paginatedPatients = filteredPatients.slice(
    page * rowsPerPage,
    page * rowsPerPage + rowsPerPage
  );

  const calculateAge = (birthDate) => {
    const today = new Date();
    const birth = new Date(birthDate);
    let age = today.getFullYear() - birth.getFullYear();
    const monthDiff = today.getMonth() - birth.getMonth();
    
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birth.getDate())) {
      age--;
    }
    
    return age;
  };

  const getGenderChip = (gender) => {
    return gender === 'male' ? (
      <Chip label="" color="primary" size="small" />
    ) : (
      <Chip label="" color="secondary" size="small" />
    );
  };

  const handleViewPatient = (patientId) => {
    navigate(`/patients/${patientId}`);
  };

  const handleCreateEncounter = (patientId) => {
    navigate(`/encounters/create?patient_id=${patientId}`);
  };

  const handleAddPatient = () => {
    navigate('/patients/create');
  };

  return (
    <Box>
      {/* Header */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <People sx={{ mr: 1, fontSize: 32 }} />
          <Typography variant="h4" component="h1">
            
          </Typography>
        </Box>
        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={handleAddPatient}
          size="large"
        >
          
        </Button>
      </Box>

      {/* Search and Summary */}
      <Grid container spacing={3} sx={{ mb: 3 }}>
        <Grid item xs={12} md={8}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
              <TextField
                fullWidth
                placeholder="ID"
                value={searchTerm}
                onChange={handleSearch}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Search />
                    </InputAdornment>
                  ),
                }}
              />
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
              <Typography variant="h3" color="primary">
                {filteredPatients.length}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                 / : {patients.length}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Patients Table */}
      <Card>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>ID</TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell align="center"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedPatients.map((patient) => (
                <TableRow key={patient.id} hover>
                  <TableCell>
                    <Typography variant="body2" fontWeight="bold">
                      {patient.patient_id}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography variant="body1">
                      {patient.last_name} {patient.first_name}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography variant="body2" color="text.secondary">
                      {patient.last_name_kana} {patient.first_name_kana}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography variant="body1">
                      {calculateAge(patient.date_of_birth)}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    {getGenderChip(patient.gender)}
                  </TableCell>
                  <TableCell>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <Phone sx={{ mr: 1, fontSize: 16, color: 'text.secondary' }} />
                      {patient.phone}
                    </Box>
                  </TableCell>
                  <TableCell>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <CalendarToday sx={{ mr: 1, fontSize: 16, color: 'text.secondary' }} />
                      {patient.date_of_birth}
                    </Box>
                  </TableCell>
                  <TableCell align="center">
                    <Tooltip title="">
                      <IconButton
                        color="primary"
                        onClick={() => handleViewPatient(patient.id)}
                        size="small"
                      >
                        <Visibility />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="">
                      <IconButton
                        color="secondary"
                        size="small"
                      >
                        <Edit />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="">
                      <IconButton
                        color="success"
                        onClick={() => handleCreateEncounter(patient.id)}
                        size="small"
                      >
                        <Assignment />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        
        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={filteredPatients.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
          labelRowsPerPage=":"
          labelDisplayedRows={({ from, to, count }) =>
            `${from}-${to} / ${count !== -1 ? count : `more than ${to}`}`
          }
        />
      </Card>

      {/* Floating Action Button */}
      <Fab
        color="primary"
        aria-label="add patient"
        sx={{ position: 'fixed', bottom: 16, right: 16 }}
        onClick={handleAddPatient}
      >
        <Add />
      </Fab>
    </Box>
  );
};

export default Patients;