import React from 'react';
import {
  Grid,
  Card,
  CardContent,
  Typography,
  Box,
  Paper,
  List,
  ListItem,
  ListItemText,
  Chip,
  Avatar,
  Divider,
} from '@mui/material';
import {
  People,
  Assignment,
  TrendingUp,
  Schedule,
  Person,
  CalendarToday,
} from '@mui/icons-material';
import { useAuth } from '../contexts/AuthContext';

// Sample data - in real app, this would come from API
const dashboardStats = [
  {
    title: '',
    value: '24',
    icon: <People />,
    color: '#1976d2',
    change: '+12%',
  },
  {
    title: '',
    value: '18',
    icon: <Assignment />,
    color: '#2e7d32',
    change: '+8%',
  },
  {
    title: '',
    value: '156',
    icon: <TrendingUp />,
    color: '#ed6c02',
    change: '+15%',
  },
  {
    title: '',
    value: '7',
    icon: <Schedule />,
    color: '#9c27b0',
    change: '-3%',
  },
];

const recentPatients = [
  {
    id: 1,
    name: ' ',
    age: 45,
    lastVisit: '2024-01-15',
    status: 'completed',
  },
  {
    id: 2,
    name: ' ',
    age: 32,
    lastVisit: '2024-01-15',
    status: 'in-progress',
  },
  {
    id: 3,
    name: ' ',
    age: 67,
    lastVisit: '2024-01-14',
    status: 'scheduled',
  },
];

const StatCard = ({ title, value, icon, color, change }) => (
  <Card sx={{ height: '100%' }}>
    <CardContent>
      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        <Box>
          <Typography color="text.secondary" gutterBottom variant="h6">
            {title}
          </Typography>
          <Typography variant="h4" component="div" fontWeight="bold">
            {value}
          </Typography>
          <Typography variant="body2" color={change.startsWith('+') ? 'success.main' : 'error.main'}>
            {change} from last week
          </Typography>
        </Box>
        <Avatar
          sx={{
            backgroundColor: color,
            width: 56,
            height: 56,
          }}
        >
          {icon}
        </Avatar>
      </Box>
    </CardContent>
  </Card>
);

const getStatusChip = (status) => {
  switch (status) {
    case 'completed':
      return <Chip label="" color="success" size="small" />;
    case 'in-progress':
      return <Chip label="" color="warning" size="small" />;
    case 'scheduled':
      return <Chip label="" color="info" size="small" />;
    default:
      return <Chip label={status} size="small" />;
  }
};

const Dashboard = () => {
  const { user } = useAuth();

  const getGreeting = () => {
    const hour = new Date().getHours();
    if (hour < 12) return '';
    if (hour < 18) return '';
    return '';
  };

  return (
    <Box>
      {/* Welcome Section */}
      <Box sx={{ mb: 4 }}>
        <Typography variant="h4" gutterBottom>
          {getGreeting()}{user?.full_name || user?.username}
        </Typography>
        <Typography variant="body1" color="text.secondary">
          
        </Typography>
      </Box>

      {/* Stats Cards */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        {dashboardStats.map((stat, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <StatCard {...stat} />
          </Grid>
        ))}
      </Grid>

      <Grid container spacing={3}>
        {/* Recent Patients */}
        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 2, height: '400px' }}>
            <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center' }}>
              <Person sx={{ mr: 1 }} />
              
            </Typography>
            <Divider sx={{ mb: 2 }} />
            <List>
              {recentPatients.map((patient, index) => (
                <ListItem key={patient.id} divider={index < recentPatients.length - 1}>
                  <Avatar sx={{ mr: 2 }}>
                    {patient.name.charAt(0)}
                  </Avatar>
                  <ListItemText
                    primary={
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                        <Typography variant="body1">{patient.name}</Typography>
                        {getStatusChip(patient.status)}
                      </Box>
                    }
                    secondary={
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                        <Typography variant="body2">
                          : {patient.age}
                        </Typography>
                        <Typography variant="body2">
                          : {patient.lastVisit}
                        </Typography>
                      </Box>
                    }
                  />
                </ListItem>
              ))}
            </List>
          </Paper>
        </Grid>

        {/* Today's Schedule */}
        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 2, height: '400px' }}>
            <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center' }}>
              <CalendarToday sx={{ mr: 1 }} />
              
            </Typography>
            <Divider sx={{ mb: 2 }} />
            <List>
              <ListItem>
                <ListItemText
                  primary="09:00 - "
                  secondary="  (P000001)"
                />
                <Chip label="" color="success" size="small" />
              </ListItem>
              <ListItem>
                <ListItemText
                  primary="10:30 - "
                  secondary="  (P000002)"
                />
                <Chip label="" color="warning" size="small" />
              </ListItem>
              <ListItem>
                <ListItemText
                  primary="14:00 - "
                  secondary="  (P000003)"
                />
                <Chip label="" color="info" size="small" />
              </ListItem>
              <ListItem>
                <ListItemText
                  primary="15:30 - "
                  secondary="  (P000004)"
                />
                <Chip label="" color="info" size="small" />
              </ListItem>
            </List>
          </Paper>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Dashboard;